import { mapGetters } from "vuex";
export default {
  data() {
    return {
      addPermModel: false,
      valid: false,
      loading:false,
      form: {
       eventname: "",
        opendate: "",
        closedate: "", 
        file:null,
      },
      snackbar: false,
      color: "",
      text: "",
      loading: false,
    };
  },
  async fetch() {
    this.overlay = true;
    this.overlay = false;
  },
  computed: {},

  methods: {
    async submit() {
      if (this.$refs.form.validate()) {
        this.valid = true;
        this.loading = true;
        try {
          const formdata = new FormData();
          formdata.append("eventname", this.form.eventname);
          formdata.append("file", this.form.file);
          formdata.append("opendate", this.form.opendate);
          formdata.append("closedate", this.form.closedate);

          await this.$axios.post("api/admin/events", formdata).then((res) => {
            this.loading = false;
            this.color = "success";
            this.snackbar = true;
            this.text = res.data.message;
            this.$store.dispatch("conferences/getConferences");
            this.$refs.form.reset();
            this.addPermModel = false;
          });
        } catch (err) {
          this.loading = false;
          this.color = "error";
          this.snackbar = true;
          this.text = err.response.data.message;
        }
      }
    },
  },
};
